import { Component, Element, Fragment, FunctionalComponent, getAssetPath, h, Host, Listen, Prop, State } from '@stencil/core';

@Component({
  tag: 'rwc-hero-banner',
  styleUrl: './hero-banner.scss',
  shadow: true,
  assetsDirs: [
    '../../assets/images/banners'
  ]
})
export class RwcHeroBanner {
  /**
   * Reference to the host element.
   */
  @Element() host: HTMLRwcHeroBannerElement;

  /**
   * The path to the image asset to render.
   */
  @Prop({ reflect: true }) src: string = getAssetPath('assets/images/banners/Banner_example.jpg');

  /**
   * The horizontal position of the center of the image in percentage.\
   * 0 - left edge, 100 - right edge, 50 - center (default) 
   */
  @Prop() horizontalCenter: number = 50;

  /**
   * The vertical position of the center of the image in percentage.\
   * 0 - top edge, 100 - bottom edge, 50 - center (default) 
   */
  @Prop() verticalCenter: number = 50;
  
  /**
   * Enables the rendering of the gradient.
   */
  @Prop() renderGradient: boolean = false;

  /**
   * Sets the breakpoint in pixels where vertical gradient stops and horizontal starts.
   */
  @Prop() gradientDirectionBreakpoint: number = 0;

  /**
   * Sets the height automatically based on window inner width.
   */
  @Prop({ reflect: true }) autoHeight: boolean = true;

  /**
   * Removes the default content HTML structure.
   */
  @Prop() customStructure: boolean = false;

  /**
   * Determines if the current gradient mode is vertical.
   */
  @State() vertical: boolean = false;
  
  /**
   * Gets the width of th host element.
   */
  private get hostWidth(): number {
    let hostWidth = this.host.offsetWidth;
    if (typeof hostWidth !== "number") {
      hostWidth = window.innerWidth;
    }
    return hostWidth;
  }

  /**
   * Handles the stencil lifecycle event called after the first load of the component.
   */
  componentDidLoad(): void {
    this.updateGradientMode();
  }

  /**
   * Handles the window resize event.
   */
  @Listen('resize', { target: 'window' })
  onWindowResize(): void {
    this.updateGradientMode();
  }

  /**
   * Updates the current gradient render mode based on window width.
   */
  updateGradientMode(): void {
    if (this.gradientDirectionBreakpoint > 0) {
      this.vertical = (this.hostWidth >= this.gradientDirectionBreakpoint ? false : true);
    }
  }

  /**
   * Renders the component.
   */
  render(): FunctionalComponent {
    return (
      <Host
        class={ {
          vertical: this.vertical
        } }
      >
        <div
          class={ {
            container: true,
            'custom': this.customStructure,
            'auto-height': this.autoHeight
          } }
          style={ {
            'background-image': `url(${ this.src })`,
            'background-position-x': `${ this.horizontalCenter }%`,
            'background-position-y': `${ this.verticalCenter }%`
          } }
        >
          { this.renderGradientElement() }
          { this.renderContent(this.customStructure) }
        </div>
      </Host >
    );
  }
  
  /**
   * Renders the gradient element.
   */
  renderGradientElement(): FunctionalComponent {
    if (this.renderGradient) {
      return (
        <div class="gradient"></div>
      )
    }
    
    return <Fragment></Fragment>
  }

  /**
   * Renders the content.
   */
  renderContent(customStructure: boolean): FunctionalComponent {
    if (customStructure) {
      return (
        <div class="content">
          <slot />
        </div>
      )
    }

    return (
      <div class="grid">
        <div class="content">
          <slot></slot>
        </div>
      </div>
    )
  }
}
