@import 'global.scss';

:host {
  /**
   * @prop --hero-banner-text-color:  The text color of the text of the banner.
   * @prop --hero-banner-font-family: The font family of the text of the banner.

   * @prop --hero-banner-container-padding: The padding of the container of the banner.

   * @prop --hero-banner-background-repeat: The background repeat of the banner background image.
   * @prop --hero-banner-background-size: The background size of the banner background image.
   
   * @prop --hero-banner-min-height: The min height of the banner.

   * @prop --hero-banner-gradient-direction: The direction of the gradient of the banner.
   * @prop --hero-banner-gradient-background-image: The background image of the gradient of the banner. Uses css linear-gradient method by default.

   * @prop --hero-banner-content-grid-column-count: The column count of the banner content.
   * @prop --hero-banner-content-grid-column: The grid column param for the content inside the grid. For larger screens.
   * @prop --hero-banner-content-grid-column--mobile: The grid column param for the content inside the grid. For smaller and mobile screens.
   * @prop --hero-banner-content-justify-content: The justify content param of the content inside the grid. Centered vertically by default.
   
   * @prop display: The display mode of the host element.
   */

  --hero-banner-text-color: #{$rwc-color-foundation-white};
  --hero-banner-font-family: #{$rwc-font-family-default};

  --hero-banner-container-padding: #{$rwc-spacing--xl};

  --hero-banner-background-repeat: no-repeat;
  --hero-banner-background-size: cover;
  
  --hero-banner-min-height: 31.25rem;

  --hero-banner-gradient-direction: 90deg;
  --hero-banner-gradient-background-image: linear-gradient(var(--hero-banner-gradient-direction), #{rgba($rwc-color-foundation-black, 1)}, #{rgba($rwc-color-foundation-black, 0)});

  --hero-banner-content-grid-column-count: 4;
  --hero-banner-content-grid-column: span 3;
  --hero-banner-content-grid-column--mobile: span 4;
  --hero-banner-content-justify-content: center;
  
  display: block;
}

:host(.vertical) {
  --hero-banner-gradient-direction: 0deg;
  --hero-banner-content-justify-content: flex-end;
}

.auto-height {
  @media all and (min-width: 30rem) {
    --hero-banner-min-height: 35rem;
  }

  @media all and (min-width: 64rem) {
    --hero-banner-min-height: 26rem;
  }
}

.container {
  background-repeat: var(--hero-banner-background-repeat);
  background-size: var(--hero-banner-background-size);
  color: var(--hero-banner-text-color);
  display: flex;
  font-family: var(--hero-banner-font-family);
  flex-direction: column;
  min-height: var(--hero-banner-min-height);
  position: relative;

  &:not(.custom) {
    padding: var(--hero-banner-container-padding);
  }
}

.gradient {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-image: var(--hero-banner-gradient-background-image);
  pointer-events: none;
}

.grid {
  display: grid;
  grid-template-columns: repeat(var(--hero-banner-content-grid-column-count), 1fr);
  flex-grow: 1;
  z-index: 1;

  .content {
    grid-column: var(--hero-banner-content-grid-column--mobile);
    display: flex;
    flex-direction: column;
    justify-content: var(--hero-banner-content-justify-content);
    z-index: 1;

    @media all and (min-width: 48rem) {
      grid-column: var(--hero-banner-content-grid-column);
    }
  }
}
